/**
 * @name Hooks
 * @type {Object}
 */

/**
 * @name Hooks#defaultSortableColumn
 *
 * @description
 * Return the key of the default sort column
 *
 * @type {function}
 * @returns {string|null} Returns column key
 */
export const defaultSortableColumn = () => 'requestedExecutionDate';

/**
 * @name Hooks#defaultSortableDirection
 *
 * @description
 * defaultSortableDirection default hook
 *
 * @type {function}
 * @returns {?string}
 */
export const defaultSortableDirection = () => 'DESC';

/**
 * @name Hooks#processPaymentOrders
 *
 * @description
 * Adds transactionInformation field to every payment order
 *
 * @type {function}
 * @param {array} paymentOrders Collection of payment orders
 * @returns {array}
 */
export const processPaymentOrders = (paymentOrders) => paymentOrders.map(paymentOrder =>
  Object.assign({},
    paymentOrder,
    { transactionInformation: paymentOrder.creditTransferTransactionInformation[0] }
  ));



