/* globals window, document */
import { formatByGroups } from 'lib-bb-iban';
import angular from 'vendor-bb-angular';
import {
  Preferences,
  Action,
  AccountSchema,
  PaymentTypeId,
  paymentStatusLookup,
} from './constants';

/**
 * @name Helpers
 * @type {Object}
 */

const headers = [
  {
    label: 'header.status',
    sortable: {
      key: 'status',
      direction: 'ASC',
    },
  },
  {
    label: 'header.debtor',
    sortable: {
      key: 'debtorName',
      direction: 'ASC',
    },
  },
  {
    label: 'header.creditor',
    sortable: {
      key: 'creditorName',
      direction: 'ASC',
    },
  },
  {
    label: 'header.currency',
    sortable: {
      key: 'currency',
      direction: 'ASC',
    },
  },
  {
    label: 'header.amount',
    class: 'text-right',
    sortable: {
      key: 'amount',
      direction: 'DESC',
    },
  },
  {
    label: 'header.exec',
    class: 'text-right',
    sortable: {
      key: 'requestedExecutionDate',
      direction: 'DESC',
    },
  },
  {
    label: 'header.actions',
    class: 'text-center',
  },
];

/**
 * @name Helpers#action
 * @type {Object}
 * @description Populate Actions's constant
 */
const action = Object.assign({}, Action);

const isSortableActive = (sortable, $ctrl) =>
  sortable && (sortable.key === $ctrl.state.paymentOrders.orderBy);

const calendarPopupConfig = {
  minDate: Preferences.MIN_DATE,
  maxMode: Preferences.MAX_MODE,
};

/**
 * @description
 * Check if value is defined, not null and not empty
 *
 * @name Helpers#hasValue
 * @type {function}
 *
 * @param {*} value  The value to check
 * @return {boolean}
 */
const hasValue = value => !!value;

/**
 * @description
 * Opens the modal window with selected Payment Order
 *
 * @name Helpers#openModalWindow
 * @type {function}
 *
 * @param {object} ext                  extension reference
 * @param {object} selectedPayment      Payment Order to be shown
 */
const openModalWindow = (ext, selectedPayment) => {
  Object.assign(ext, { showModal: true, payment: selectedPayment });
};

/**
 * @description
 * Toggle row details visibility
 *
 * @name Helpers#closeModalWindow
 * @type {function}
 *
 * @param {object} ext                extension reference
 */
const closeModalWindow = (ext) => {
  Object.assign(ext, { showModal: false, payment: null });
};

/**
 * @name Helpers#isPaginationTypeMatch
 *
 * @description
 * Checks if actual pagination type matches the one, defined in properties
 *
 * @type {Function}
 * @param {Function} $ctrl      Current controller
 * @param {String} type         Description of pagination type (pagination, load-more)
 *
 * @returns {Boolean}
 */
const isPaginationTypeMatch = ($ctrl, type) =>
  $ctrl.state.pagination.paginationType === type;

/**
 * @name Helpers#getSearchQuery
 * @type {function}
 * @param {function} $ctrl      Current controller
 * @description
 * If search enabled returns current search query, otherwise false
 * @returns {string|boolean}
 */
const getSearchQuery = ($ctrl) =>
  $ctrl.state.paymentOrders.searching && $ctrl.state.search.query;

export default (context) => {
  const i18n = context.$filter('i18n');

  /**
   * @name Helpers#searchLabelsTranslations
   * @type {function}
   * @description
   * Returns object with translated labels necessary for search box component
   * @returns {object}
   */
  const searchLabelsTranslations = () => ({
    title: i18n('label.search.title'),
    placeholder: i18n('label.search.placeholder'),
    clear: i18n('label.search.clear'),
    submit: i18n('label.search.submit'),
  });

  /**
   * @name Helpers#thereAreMatches
   * @type {function}
   * @description
   * Checks if there has been any match result when searching
   * @param {object} $ctrl Current controller
   * @returns {boolean}
   */
  const thereAreMatches = ($ctrl) => Boolean(
    !$ctrl.state.paymentOrders.loading &&
    $ctrl.state.paymentOrders.searching &&
    $ctrl.state.pagination.totalItems
  );

  /**
   * @name ifSearchingThen
   * @inner
   * @type {function}
   * @description
   * Returns `$then` if it is searching `$else` otherwise
   * @param {object} $ctrl Current controller
   * @param {*} $then It can be anything
   * @param {*} $else It can be anything
   * @returns {*}
   */
  const ifSearchingThen = ($ctrl, $then, $else) =>
    ($ctrl.state.paymentOrders.searching ? $then : $else);

  /**
   * @name Helpers#getEmptyIcon
   * @type {function}
   * @description
   * Returns correct icon to be used when empty results
   * @param {object} $ctrl Current controller
   * @returns {boolean}
   */
  const getEmptyIcon = ($ctrl) =>
    ifSearchingThen($ctrl, 'fas fa-search', 'far fa-money-bill-alt');

  /**
   * @name Helpers#getEmptyTitle
   * @type {function}
   * @description
   * Returns translated title to be used when empty results
   * @param {object} $ctrl Current controller
   * @returns {boolean}
   */
  const getEmptyTitle = ($ctrl) =>
    i18n(ifSearchingThen($ctrl, 'label.search.empty.title', 'label.noItems.title'));

  /**
   * @name Helpers#getEmptyDescription
   * @type {function}
   * @description
   * Returns translated description to be used when empty results
   * @param {object} $ctrl Current controller
   * @returns {boolean}
   */
  const getEmptyDescription = ($ctrl) =>
    i18n(ifSearchingThen($ctrl, 'label.search.empty.description', 'label.noItems.description'));

  /**
   * @name Helpers#toggleFilter
   * @type {function}
   *
   * @description
   * Toggle filter component.
   *
   * @public
   * @returns {boolean} A status of filter component
   */
  const toggleFilter = ext => (Object.assign(ext, { showFilter: !ext.showFilter }));

  /**
   * @name Helpers#canPerformAction
   * @type {Function}
   * @description
   * Checks if the current user able to perform target action per `payment`
   *
   * @param {Object} payment - The object payment to check
   * @param {String} actionName - target action name
   *
   * @returns {Boolean} Flag indicating if able to perform the target action
   */
  const canPerformAction = (payment, actionName) =>
    Boolean(Array.isArray(payment.actions) && payment.actions.indexOf(actionName) >= 0);

  /**
   * @name Helpers#authorize
   * @type {function}
   * @description
   * Initiate payment order authorize procedure
   *
   * @param {object} $ctrl Current controller
   * @param {object} payment payment object
   * @param {object} e DOM click event
   */
  const authorize = ($ctrl, payment, e) => {
    e.stopPropagation();
    if (canPerformAction(payment, Action.APPROVE)) {
      $ctrl.authorize(payment.id);
    }
  };

  /**
   * @name Helpers#rejectInit
   * @type {function}
   * @description
   * Open rejection popup
   *
   * @param {object} payment object
   * @param {object} e DOM click event
   * @param {object} ext extension
   */
  const rejectInit = (payment, e, ext) => {
    e.stopPropagation();
    if (canPerformAction(payment, Action.REJECT)) {
      Object.assign(ext, {
        rejectOpened: true,
        rejectPaymentId: payment.id,
      });
    }
  };

  /**
   * @name Helpers#removeNotification
   * @type {function}
   *
   * @description
   * Helper to remove notification from the list
   *
   * @param {object} notification
   * @param {Array} notificationsList
   * @returns {Array}
   */
  const removeNotification = (notification, notificationsList) =>
    notificationsList.filter(item => item !== notification);

  /**
   * @name Helpers#formatAccountNumber
   * @type {Function}
   *
   * @description
   * Splits the string up with spaces by groups of 4.
   * "IE47FNLL45049097007367" => "IE47 FNLL 4504 9097 0073 67"
   *
   * @param {String} str Account number
   * @param {String} symbol Separator symbol
   * @param {Number} groupLen Number of digits to group by
   * @return {String} Formatted account number
   *
   * @deprecated [A new function introduced (getAccountNumber)]
   */
  const formatAccountNumber = formatByGroups;

  /**
   *
   * @name Helpers#formatBySchema
   * @type {Object}
   *
   * @inner
   *
   * @description
   * Map account's schemaName to a function that accepts the account number and
   * return it formatted or not based on the schemaName.
   *
   * It only formats account number with IBAN schema.
   *
   * Formatting: Splits the string up with spaces by groups of 4.
   * "IE47FNLL45049097007367" => "IE47 FNLL 4504 9097 0073 67"
   *
   */
  const formatBySchema = {
    [AccountSchema.IBAN]: identification => formatByGroups(identification),
    [AccountSchema.BBAN]: identification => identification,
  };

  /**
   * @name Helpers#getAccountNumber
   * @type {Function}
   *
   * @description
   * It accepts the identification Object for creditor or debtor account.
   * Map the (schemeName) to the expected format using (formatBySchema) Object
   *
   * @param {Object} accountIdentification - Creditor or Debtor identification Object
   * that includes the schemaName and the identification (account number)
   *
   * @return {String} account number formated or not based on the schema
   */
  const getAccountNumber = accountIdentification => {
    const { schemeName, identification } = accountIdentification;
    return formatBySchema[schemeName](identification);
  };

  /**
   * @name Helpers#isSelected
   *
   * @description
   * Helper to know if the payment is selected.
   *
   * @param {Object} payment
   * @param {Object} selState
   * @type {Function}
   * @returns {Boolean}
   */
  const isSelected = (payment, selState) =>
    selState.selectedPaymentsIds.includes(payment.id);

  /**
   * @name Helpers#isAnySelected
   *
   * @description
   * Helper to know if any payment is selected.
   *
   * @param {object} selState
   * @type {function}
   * @returns {boolean}
   */
  const isAnySelected = selState =>
    selState.selectedPayments.length > 0;

  /**
   * @name Helpers#howManySelected
   *
   * @description
   * Helper to know how many payments are selected.
   *
   * @param {object} selState
   * @type {function}
   * @returns {boolean}
   */
  const howManySelected = selState =>
    selState.selectedPayments.length;

  /**
   * @name Helpers#listPaymentsIDs
   * @type {Function}
   *
   * @inner
   * @description build a list of payment orders' IDs
   *
   * @param {Array} payments - array of payments
   *
   * @returns {Array} payment orders' IDs
   */
  const listPaymentsIDs = payments => payments.map(p => p.id);

  /**
   * @name Helpers#isSelectedAllStrict
   * @inner
   *
   * @description
   * Strictly compare selected items ids and model list
   *
   * @param {array} payments
   * @param {object} selState
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedAllStrict = (payments, selState) => {
    const selected = selState.selectedPaymentsIds;

    if (payments.length !== selected.length) {
      return false;
    }

    const matches = payments.filter(item => selected.indexOf(item.id) !== -1);
    return matches.length === payments.length;
  };

  /**
   * @name Helpers#isSelectedAll
   *
   * @description
   * Helper to know if ALL payments are selected.
   *
   * @param {array} payments
   * @param {object} selState
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedAll = (payments, selState) =>
    payments && payments.length > 0 && isSelectedAllStrict(payments, selState);

  /**
   * @name Helpers#updateSelectedPaymentState
   * @type {Function}
   * @inner
   *
   * @description
   * Update the selected payments state based on available actions "Approve & reject"
   *
   * @param {Object} selectionState
   * @param {Object} stateUpdate
   */
  const updateSelectedPaymentState = (selectionState, stateUpdate) => {
    const { selectedPayments } = { ...stateUpdate };

    const selectedPaymentsActionGrouped = {
      [Action.APPROVE]: [],
      [Action.REJECT]: [],
    };

    selectedPayments.forEach(singleSelectedPayment => {
      if (canPerformAction(singleSelectedPayment, Action.APPROVE)) {
        selectedPaymentsActionGrouped[Action.APPROVE].push(singleSelectedPayment);
      }

      if (canPerformAction(singleSelectedPayment, Action.REJECT)) {
        selectedPaymentsActionGrouped[Action.REJECT].push(singleSelectedPayment);
      }
    });

    Object.assign(selectionState, {
      ...stateUpdate,
      selectedPaymentsActionGrouped,
      selectedPaymentsIds: listPaymentsIDs(selectedPayments),
    });
  };

  /**
   * @name Helpers#isSelectedPart
   *
   * @description
   * Helper to know if at least one payment is selected.
   *
   * @param {array} payments
   * @param {object} selState
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedPart = (payments, selState) =>
    isAnySelected(selState) && !isSelectedAll(payments, selState);

  /**
   * @name Helpers#toggleAllPayments
   *
   * @description
   * Helper to select/deselect all payments loaded.
   *
   * @param {array} payments
   * @param {object} selState
   * @type {function}
   * @returns {null}
   */
  const toggleAllPayments = (payments, selState) => {
    const state = selState;
    const stateUpdate = {};

    if (isSelectedAll(payments, selState)) {
      Object.assign(stateUpdate, { selectedPayments: [] });
    } else {
      Object.assign(stateUpdate, { selectedPayments: [...payments] });
    }

    updateSelectedPaymentState(state, stateUpdate);
  };

  /**
   * @name Helpers#deselectPayments
   *
   * @description
   * Helper to deselect all payments selected.
   *
   * @param {object} selState
   * @type {function}
   * @returns {null}
   */
  const deselectPayments = selState => {
    updateSelectedPaymentState(selState, { selectedPayments: [] });
  };

  /**
   * @name Helpers#toggleSelection
   *
   * @description
   * Helper to select/deselect single payment.
   *
   * @param {object} payment
   * @param {object} selState
   * @param {object} $event DOM click event
   * @type {function}
   * @returns {null}
   */
  const toggleSelection = (payment, selState, $event) => {
    $event.stopPropagation();
    const state = selState;
    const stateUpdate = {};

    if (isSelected(payment, state)) {
      Object.assign(stateUpdate, {
        selectedPayments: state.selectedPayments.filter(item => item.id !== payment.id),
      });
    } else {
      Object.assign(stateUpdate, {
        selectedPayments: [
          ...state.selectedPayments,
          payment,
        ],
      });
    }

    updateSelectedPaymentState(state, stateUpdate);
  };

  /**
   * @name Helpers#confirmPaymentCancel
   * @type {Function}
   * @description
   * Open confirmation modal for cancel payment
   * populate target paymentId & version
   *
   * @param {Object} payment object
   * @param {Object} event DOM click event
   * @param {Object} ext extension
   */
  const confirmPaymentCancel = (event, payment, ext) => {
    event.stopPropagation();
    if (canPerformAction(payment, Action.CANCEL)) {
      Object.assign(ext, {
        cancelOpened: true,
        cancelPaymentId: payment.id,
        paymentVersion: payment.version,
      });
    }
  };

  /**
   * @name Helpers#confirmPaymentDelete
   * @type {Function}
   * @description
   * Open confirmation modal for delete payment
   * populate target paymentId
   *
   * @param {Object} payment object
   * @param {Object} event DOM click event
   * @param {Object} ext extension
   */
  const confirmPaymentDelete = (event, payment, ext) => {
    event.stopPropagation();
    if (canPerformAction(payment, Action.DELETE)) {
      Object.assign(ext, {
        deleteOpened: true,
        deletePaymentId: payment.id,
        paymentVersion: payment.version,
      });
    }
  };

  /**
   * @name Helpers#getAddressText
   *
   * @description
   * Helper to format address data
   *
   * @type {function}
   *
   * @param {Object} postalAddress
   * @returns {string}
   */
  const getAddressText = (postalAddress) => [
    postalAddress.addressLine1,
    postalAddress.addressLine2,
    postalAddress.streetName,
    postalAddress.postCode,
    postalAddress.town,
    postalAddress.countrySubDivision,
  ].reduce((accumulator, currentValue) => {
    if (typeof currentValue === 'string' && currentValue.trim()) {
      accumulator.push(currentValue.trim());
    }
    return accumulator;
  }, []).join(', ');

  /**
   * @name Helpers#getIntermediaryBankType
   *
   * @description
   * Helper to get bank type based on provided code
   *
   * @type {function}
   *
   * @param {Object} intermediaryBank
   * @returns {string}
   */
  const getIntermediaryBankType = (intermediaryBank) =>
    (intermediaryBank.bic ? 'SWIFT/BIC' : 'ABA');

  /**
   * @description
   * Return list of status filters.
   * Filter paymentStatusLookup and reture all statuses that can be used as filters
   *
   * @name Helpers#paymentStatusFiltersLookup
   * @type {function}
   *
   * return {Object[]} Array of different payment status lookup Object
   * ex: [{icon: 'warning', key: 'CANCELLED', name: 'cancelled', isFilter: true}]
   */
  const paymentStatusFiltersLookup = () => {
    let statusFilters = [];

    angular.forEach(paymentStatusLookup, (value, key) => {
      if (value.isFilter) {
        statusFilters = [...statusFilters, Object.assign(value, { key })];
      }
    });

    return statusFilters;
  };

  /**
   * @description
   * return the icon name/class from the matching payment status lookup object
   * It returns 'secondary' by default
   *
   * @name Helpers#getStatusIcon
   * @type {function}
   *
   * return {String} ex: 'warning' || 'danger' ...
   */
  const getStatusIcon = statusKey => paymentStatusLookup[statusKey].icon || 'secondary';

  /**
   * @name Helpers#getStatusLabelKey
   * @type {function}
   *
   * @description
   * Find the matching object in paymentStatusLookup by the status key (ex: 'APPROVED'),
   * Extract the name attribute
   * Generate the correct key for translation message
   *
   * @param {String} status property on payment object
   * @param {Boolean} enble/disable translation key postfix ('.short')
   *
   * @returns {string} Translation key for status label
   */
  const getStatusLabelKey = (status, postfix) => {
    let traslationKeyPostfix = '';

    if (postfix === true) {
      traslationKeyPostfix = '.short';
    }

    return `payment.status.${paymentStatusLookup[status].name}${traslationKeyPostfix}`;
  };

  /**
   * @name Helpers#canPerformActionForBulk
   * @type {Function}
   *
   * @description
   * Check if can perform an action "approve or reject" on the selected payment orders
   *
   * @param {Object} state
   * @param {String} actionName
   *
   * @returns {Boolean}
   */
  const canPerformActionForBulk = (state, actionName) => {
    const { selections } = state;
    const actionGroup = selections.selectedPaymentsActionGrouped[actionName];
    return Boolean(
      actionGroup &&
      actionGroup.length &&
      actionGroup.length === selections.selectedPayments.length
    );
  };

  /**
   * @name Helpers#confirmActionOnBulk
   * @type {Function}
   *
   * @description
   * Open the confirmation modal window based on action "approve or reject"
   *
   * @param {Object} ext extension
   * @param {String} actionName
   */
  const confirmActionOnBulk = (ext, actionName) => {
    if (actionName === Action.APPROVE) {
      Object.assign(ext, { confirmBulkApprove: true });
    }

    if (actionName === Action.REJECT) {
      Object.assign(ext, { confirmBulkReject: true });
    }
  };

  /**
   * @name Helpers#showBulkActionDetails
   * @type {function}
   *
   * @description
   * Show a modal window that contain bulk action warning details
   *
   * @param {Object} ext
   * @param {Object} payload
   */
  const showBulkActionDetails = (ext, payload) => {
    Object.assign(ext, { showBulkActionDetails: true, context: payload });
  };

  /**
   * @name Helpers#dismissBulkActionWarning
   * @type {function}
   *
   * @description
   * Dismiss the warning alert for bulk action by updating the state
   *
   * @param {Object} $ctrl
   */
  const dismissBulkActionWarning = ($ctrl) => {
    Object.assign($ctrl.state, {
      bulkActionNotification: {
        isVisible: false,
      },
    });
  };

  /**
   * @name Helpers#printModalWindow
   * @type {Function}
   *
   * @description
   * Initiates printing dialog to print modal content and adds a
   * fallback for Safari browsers (it fails to print popup content with
   * default window.print() method).
   *
   * @param {String} contentElQuery DOM query to printable content
   */
  const printModalWindow = (contentElQuery) => {
    if (!window.safari) {
      window.print();
    } else {
      // Safari browser fallback
      const divToPrint = document.querySelector(contentElQuery).innerHTML;
      const cssLinkList = document.querySelectorAll('link[rel=stylesheet]');
      const popupWin = window.open('', '_blank', 'width=700,height=800');

      popupWin.document.writeln(divToPrint);

      [...cssLinkList].forEach((node) => {
        const head = popupWin.document.head;
        const link = popupWin.document.createElement('link');

        link.type = 'text/css';
        link.rel = 'stylesheet';
        link.href = node.href;

        head.appendChild(link);
      });

      popupWin.focus();
      popupWin.print();

      setTimeout(() => {
        popupWin.close();
      }, 1000);
    }
  };

  return {
    headers,
    action,
    isSortableActive,
    hasValue,
    openModalWindow,
    closeModalWindow,
    isPaginationTypeMatch,
    searchLabelsTranslations,
    thereAreMatches,
    getSearchQuery,
    getEmptyIcon,
    getEmptyTitle,
    getEmptyDescription,
    toggleFilter,
    calendarPopupConfig,
    authorize,
    rejectInit,
    canPerformAction,
    removeNotification,
    formatAccountNumber,
    getAccountNumber,
    get paymentTypeId() { return PaymentTypeId; },

    isSelected,
    isAnySelected,
    howManySelected,
    isSelectedAll,
    isSelectedPart,
    toggleAllPayments,
    deselectPayments,
    toggleSelection,

    confirmPaymentCancel,
    getAddressText,
    getIntermediaryBankType,

    paymentStatusFiltersLookup,
    getStatusIcon,
    getStatusLabelKey,

    confirmPaymentDelete,

    canPerformActionForBulk,
    confirmActionOnBulk,
    showBulkActionDetails,
    dismissBulkActionWarning,
    printModalWindow,
  };
};



